body {
    margin: 0 auto;
}
.zone {
    color:#FFF;
    font-size:2em;
    border-radius:4px;
    border:1px solid #bbb;
    transition: all 0.3s linear;
}
.nav {
    height: 5vh; 
    display: flex;
    list-style: none;
    margin: 0;
    font-size: 1.2em;    
    min-height: 60px;
    color:#FFF;
    border-radius:4px;
    border:1px solid #bbb;
    transition: all 0.3s linear;
}
.card {
    position: absolute;
    top:0;
    bottom: 0;
    left:0;
    right: 0;
    background: black;
    opacity: 90%;
}
.cardx {
    font-size: 3em;
    float: right;
    margin-right: 50px;
    margin-top: 40px;
    clear: both;
}
.center { 
    position: absolute;
    top: 30%;
    left: 30%;
    right: 30%;
    bottom: 30%;
    text-align: center;
}

@media only screen and (max-width: 700px) {
    .nav {
        font-size: 0.5em;
        padding: 0;
    }
    .center { 
        position: absolute;
        top: 20%;
        left: 10%;
        right: 10%;
        bottom: 10%;
        text-align: center;
    }
}

.push {
    margin-left: auto;
}

li {
    padding: 20px;
}

li div {
    color: #f5f5f6;
    text-decoration: none;
    cursor: pointer;
}
.main {
    height: 94.8vh; 
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100px;
}
.grid-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 20px;
}
.bgblack {
    background: #56B870; /* Old browsers */
    background: -moz-linear-gradient(top, #56B870 0%, #a5c956 100%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#56B870), color-stop(100%,#a5c956)); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #56B870 0%,#a5c956 100%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, #56B870 0%,#a5c956 100%); /* Opera 11.10+ */
    background: -ms-linear-gradient(top, #56B870 0%,#a5c956 100%); /* IE10+ */
    background: linear-gradient(to left, #56B870 0%,#a5c956 100%); /* W3C */
}
